import React from 'react';
import { IDENTS } from 'components/SVGs/Idents';
import { SVGVariant } from 'components/SVGs/SVGProps';
import Icons from 'components/SVGs/Icons';

export type IdentValue = typeof IDENTS[number];

type Props = Readonly<{
  name: IdentValue;
  width?: number;
  height?: number;
  fillOpacity?: number;
  variant?: SVGVariant;
}>;

const Icon = ({ name, ...rest }: Props) => {
  const IconComponent = Icons[name];

  return <IconComponent {...rest} />;
};

export default Icon;
