import React from 'react';
import { View, Text, TextInput, TouchableOpacity, Platform, ScrollView } from 'react-native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { Divider, Switch } from 'react-native-paper';
import { Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { I18n } from 'i18n-js';
import { CreateEditPasswordConfigurator } from 'components/CreateEditPasswordConfigurator/createEditPasswordConfigurator';
import { PasswordConfigurator } from 'components/PasswordConfigurator/passwordConfigurator';
import { CustomSnackbar } from 'components/CustomSnackbar/customSnackbar';
import SliderContainer from 'components/SliderContainer/sliderContainer';
import { appColors } from 'utils/constants';
import { cardView, shadow, screen, passwordStyle, configuration } from './styles';

const webStyles: string =
  Platform.OS === 'web'
    ? `
    self-center
    h-full
    w-full
    sm:border-l-2
    sm:border-r-2
    sm:border-[#3091e0]
    sm:w-11/12
    md:w-1/2
    lg:w-5/12
    xl:w-[28%]`
    : '';

export const PasswordGenerator = ({
  navigation,
  scrollViewRef,
  switchEnabled,
  mPassword,
  snackbarMessage,
  snackbarVisible,
  passwordFromState,
  isCreateMode,
  isEditMode,
  translations,
  modalTranslations,
  handleCopyButton,
  handleRefreshButton,
  handleOnDismissSnackbar,
  handleOnChangePassword,
  handleOnBlurPassword,
  handleSwitch,
}: Props) => (
  <View className={webStyles}>
    <ScrollView ref={scrollViewRef}>
      <View style={screen.container}>
        <View style={[cardView.container, passwordStyle.container, shadow.container]}>
          <View style={passwordStyle.inputContainer}>
            <TextInput
              showSoftInputOnFocus={switchEnabled}
              caretHidden={!switchEnabled}
              style={passwordStyle.input}
              value={switchEnabled ? mPassword : passwordFromState}
              onChangeText={handleOnChangePassword}
              onBlur={handleOnBlurPassword}
            />
            <View style={passwordStyle.icons}>
              <TouchableOpacity onPress={handleCopyButton}>
                <MaterialCommunityIcons
                  style={{ marginRight: 10 }}
                  name='content-copy'
                  size={24}
                  color='grey'
                />
              </TouchableOpacity>
              <TouchableOpacity onPress={handleRefreshButton}>
                <Ionicons name='reload' size={24} color='grey' />
              </TouchableOpacity>
            </View>
          </View>
        </View>

        <View style={[cardView.container, configuration.container, shadow.container]}>
          <View style={configuration.textContainer}>
            <Text style={configuration.textHeader}>{translations.t('heading')}</Text>
          </View>

          <Divider style={{ backgroundColor: 'grey' }} />

          {isCreateMode && (
            <>
              <View style={configuration.switchToggle}>
                <Text style={{ fontSize: 17 }}>{translations.t('placeOwnPassword')}</Text>
                <Switch
                  style={{ marginLeft: 10 }}
                  value={switchEnabled}
                  onValueChange={handleSwitch}
                  color={appColors.primary}
                />
              </View>

              <Divider style={{ backgroundColor: 'grey' }} />
            </>
          )}

          <View style={configuration.passwordLengthContainer}>
            <Text style={{ fontSize: 17 }}>{translations.t('passwordLength')}</Text>
            <View style={configuration.lengthSliderContainer}>
              <TextInput
                showSoftInputOnFocus={false}
                caretHidden
                style={configuration.inputLength}
                keyboardType='numeric'
                value={passwordFromState.length.toString()}
              />
              <SliderContainer
                switchEnabled={switchEnabled}
                defaultValue={isEditMode ? passwordFromState.length : 10}
              />
            </View>
          </View>

          <Divider style={{ backgroundColor: 'grey' }} />

          <PasswordConfigurator switchEnabled={switchEnabled} translations={translations} />
        </View>

        {(isCreateMode || isEditMode) && (
          <CreateEditPasswordConfigurator
            navigation={navigation}
            translations={translations}
            modalTranslations={modalTranslations}
          />
        )}
      </View>
    </ScrollView>

    <CustomSnackbar
      message={snackbarMessage}
      isSnackbarVisible={snackbarVisible}
      onDismiss={handleOnDismissSnackbar}
    />
  </View>
);

interface Props {
  navigation: DrawerNavigationProp<any> | NativeStackNavigationProp<any>;
  scrollViewRef: React.RefObject<ScrollView>;
  switchEnabled: boolean;
  mPassword: string;
  snackbarMessage: string;
  snackbarVisible: boolean;
  passwordFromState: string;
  isCreateMode: boolean;
  isEditMode: boolean;
  translations: I18n;
  modalTranslations: I18n;
  handleCopyButton: () => void;
  handleRefreshButton: () => void;
  handleOnDismissSnackbar: () => void;
  handleOnChangePassword: () => void;
  handleOnBlurPassword: () => void;
  handleSwitch: () => void;
}
