import React, { Dispatch, useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { TextInputKeyPressEventData } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import {
  selectLocale,
  selectRequest,
  setIsRequest,
} from 'reduxStore/slices/applicationStatusSlice';
import { resetSnackbar, selectSnackbarVisible } from 'reduxStore/slices/uiElementsSlice';
import { signIn } from 'services/firebase';
import { Login } from 'screens/Login/login';
import useLogin from 'hooks/useLogin';
import getTranslations from 'utils/getTranslations';
import loginSignupTranslations from 'languages/loginSignupTranslations';
import { I18n } from 'i18n-js';

const LoginContainer = ({ children }: { children: React.FunctionComponent }) => {
  const [username, setUsername] = useState<string>('');
  const navigation: DrawerNavigationProp<any> = useNavigation();
  const dispatch: Dispatch<AnyAction> = useDispatch();

  const isRequesting: boolean = useSelector(selectRequest);
  const snackbarVisible: boolean = useSelector(selectSnackbarVisible);
  const touchableRef = useRef(null);
  const locale: string = useSelector(selectLocale);
  const translations: I18n = getTranslations(loginSignupTranslations, locale);

  useEffect(() => {
    dispatch(setIsRequest());
  }, []);

  useLogin();

  const handleLogin = () => signIn(username);

  const handleOnKeyPress = ({ nativeEvent }: { nativeEvent: TextInputKeyPressEventData }) => {
    if (nativeEvent.key === 'Enter') handleLogin();
  };

  const handleOnChangeText = (text: string) => setUsername(text);
  const handleOnDismiss = () => dispatch(resetSnackbar());

  return children({
    navigation,
    translations,
    username,
    isRequesting,
    snackbarVisible,
    touchableRef,
    handleOnChangeText,
    handleLogin,
    handleOnKeyPress,
    handleOnDismiss,
  });
};

const LoginContainerWrapper = () => (
  <LoginContainer>{(renderProps: any) => <Login {...renderProps} />}</LoginContainer>
);

export default LoginContainerWrapper;
