import React, { useEffect, useRef } from 'react';
import { Animated, View } from 'react-native';
import { NextButton } from 'components/OnBoarding/NextButton/nextButton';

const NextButtonContainer = ({
  scrollTo,
  percentage,
  children,
}: {
  scrollTo: () => void;
  percentage: number;
  children: React.FunctionComponent;
}) => {
  const size: number = 80;
  const strokeWidth: number = 2;
  const center: number = size / 2;
  const radius: number = size / 2 - strokeWidth / 2;
  const circumference: number = 2 * Math.PI * radius;

  const progressAnimation = useRef(new Animated.Value(0)).current;
  const progressRef = useRef<View>(null);

  const animation = (toValue: any) =>
    Animated.timing(progressAnimation, {
      toValue,
      duration: 250,
      useNativeDriver: true,
    }).start();

  useEffect(() => {
    animation(percentage);
  }, [percentage]);

  useEffect(() => {
    progressAnimation.addListener(
      (value) => {
        const strokeDashoffset = circumference - (circumference * value.value) / 100;

        if (progressRef?.current) {
          progressRef.current.setNativeProps({
            strokeDashoffset,
          });
        }
      },
      // @ts-ignore
      [percentage]
    );

    return () => {
      progressAnimation.removeAllListeners();
    };
  }, []);

  return children({
    size,
    strokeWidth,
    center,
    radius,
    circumference,
    progressRef,
    scrollTo,
  });
};

const NextButtonContainerWrapper = ({
  scrollTo,
  percentage,
}: {
  scrollTo: () => void;
  percentage: number;
}) => (
  <NextButtonContainer scrollTo={scrollTo} percentage={percentage}>
    {(renderProps: any) => <NextButton {...renderProps} />}
  </NextButtonContainer>
);

export default NextButtonContainerWrapper;
