import React, { useLayoutEffect } from 'react';
import { useSelector } from 'react-redux';
import { I18n } from 'i18n-js';
import { selectIsEditMode } from 'reduxStore/slices/passwordSlice';
import { selectUserId } from 'reduxStore/slices/userSlice';
import { selectLocale } from 'reduxStore/slices/applicationStatusSlice';
import { useNavigation } from '@react-navigation/native';
import getTranslations from 'utils/getTranslations';
import passwordGenTranslations from 'languages/passwordGenTranslations';

const useGeneratorPageTitle = () => {
  const navigation = useNavigation();
  const userId: string = useSelector(selectUserId);
  const isEditMode: boolean = useSelector(selectIsEditMode);
  const locale: string = useSelector(selectLocale);
  const translations: I18n = getTranslations(passwordGenTranslations, locale);

  const screenTitle = isEditMode
    ? translations.t('editTitle')
    : `${translations.t('generatorTitle')} ${!userId ? translations.t('isEdit') : ''}`;

  useLayoutEffect(() => {
    navigation.setOptions({
      title: screenTitle,
    });
  });

  return true;
};

export default useGeneratorPageTitle;
