import React, { Dispatch } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { View } from 'react-native';
import { I18n } from 'i18n-js';

import { CustomCheckBox } from 'components/PasswordConfigurator/CustomCheckBox/customCheckBox';
import { checkBox } from 'screens/PasswordGenerator/styles';
import {
  setIsEasy2Read,
  setIsAllChar,
  setIsUpperCase,
  setIsLowerCase,
  setIsNumbers,
  setIsSymbols,
  setIsUpperCaseAndIsLowerCase,
  setIsNumbersAndIsSymbols,
  selectIsEasy2Read,
  selectIsAllChar,
  selectIsUpperCase,
  selectIsLowerCase,
  selectIsNumbers,
  selectIsSymbols,
} from 'reduxStore/slices/configuratorSlice';
import { generatePassword } from 'reduxStore/slices/passwordSlice';

import {
  handleLeftCheckboxes,
  handleRightCheckboxes,
  updateConfiguratorStateAndGeneratePassword,
} from 'utils/configuratorUtils';
import { appColors } from 'utils/constants';

/* Reference for checkbox usage: [
  https://docs.expo.dev/versions/latest/sdk/checkbox/,
  https://snack.expo.dev/
]
 */

interface Props {
  switchEnabled: boolean;
  translations: I18n;
}

interface Checkbox {
  label: string;
  isChecked: boolean;
  disabled?: boolean;
  onPress: () => void;
}

export const PasswordConfigurator = ({ switchEnabled, translations }: Props) => {
  const dispatch: Dispatch<AnyAction> = useDispatch();

  const isEasy2Read: boolean = useSelector(selectIsEasy2Read);
  const isAllChar: boolean = useSelector(selectIsAllChar);

  const isUpperCase: boolean = useSelector(selectIsUpperCase);
  const isLowerCase: boolean = useSelector(selectIsLowerCase);
  const isNumbers: boolean = useSelector(selectIsNumbers);
  const isSymbols: boolean = useSelector(selectIsSymbols);

  const handleRightCheckboxesGivenLeftCheckboxes = () => {
    dispatch(setIsUpperCaseAndIsLowerCase({ isUpperCase: true, isLowerCase: true }));

    if (!isEasy2Read || isAllChar) {
      dispatch(setIsNumbersAndIsSymbols({ isNumbers: false, isSymbols: false }));
    } else {
      dispatch(setIsNumbersAndIsSymbols({ isNumbers: true, isSymbols: true }));
    }

    dispatch(generatePassword());
  };

  const leftCheckboxes: Array<Checkbox> = [
    {
      label: translations.t('checkboxEasy2Read'),
      isChecked: isEasy2Read,
      onPress: () => {
        handleLeftCheckboxes(
          dispatch,
          setIsEasy2Read,
          setIsAllChar,
          'isEasy2Read',
          'isAllChar',
          !isEasy2Read,
          handleRightCheckboxesGivenLeftCheckboxes
        );
      },
    },
    {
      label: translations.t('checkboxAllChar'),
      isChecked: isAllChar,
      onPress: () => {
        handleLeftCheckboxes(
          dispatch,
          setIsAllChar,
          setIsEasy2Read,
          'isAllChar',
          'isEasy2Read',
          !isAllChar,
          handleRightCheckboxesGivenLeftCheckboxes
        );
      },
    },
  ];

  const rightCheckboxes: Array<Checkbox> = [
    {
      label: translations.t('checkboxUppercase'),
      isChecked: isUpperCase,
      onPress: () => {
        handleRightCheckboxes(
          !isUpperCase,
          isLowerCase,
          isNumbers,
          isSymbols,
          dispatch,
          setIsUpperCase,
          { isUpperCase: !isUpperCase }
        );
      },
    },
    {
      label: translations.t('checkboxLowercase'),
      isChecked: isLowerCase,
      onPress: () => {
        handleRightCheckboxes(
          !isLowerCase,
          isUpperCase,
          isNumbers,
          isSymbols,
          dispatch,
          setIsLowerCase,
          { isLowerCase: !isLowerCase }
        );
      },
    },
    {
      label: translations.t('checkboxNumbers'),
      isChecked: isNumbers,
      disabled: !isAllChar || switchEnabled,
      onPress: () => {
        handleRightCheckboxes(
          !isNumbers,
          isUpperCase,
          isLowerCase,
          isSymbols,
          dispatch,
          setIsNumbers,
          {
            isNumbers: !isNumbers,
          }
        );
      },
    },
    {
      label: translations.t('checkboxSymbols'),
      isChecked: isSymbols,
      disabled: !isAllChar || switchEnabled,
      onPress: () => {
        handleRightCheckboxes(
          !isSymbols,
          isUpperCase,
          isLowerCase,
          isNumbers,
          dispatch,
          setIsSymbols,
          {
            isSymbols: !isSymbols,
          }
        );
      },
    },
  ];

  return (
    <View style={checkBox.container}>
      {/* Left checkboxes */}
      <View style={{ flexDirection: 'column', marginLeft: -20 }}>
        {leftCheckboxes.map(({ label, isChecked, onPress }, index) => (
          <View key={index} style={checkBox.section}>
            <CustomCheckBox
              label={label}
              isChecked={isChecked}
              disabled={switchEnabled}
              color={appColors.primary}
              onPress={onPress}
            />
          </View>
        ))}
      </View>

      {/* Right checkboxes */}
      <View style={{ flexDirection: 'column' }}>
        {rightCheckboxes.map(({ label, isChecked, disabled, onPress }, index) => (
          <View key={index} style={checkBox.section}>
            <CustomCheckBox
              label={label}
              isChecked={isChecked}
              disabled={disabled || switchEnabled}
              color={appColors.primary}
              onPress={() => {
                onPress();
                updateConfiguratorStateAndGeneratePassword(dispatch);
              }}
            />
          </View>
        ))}
      </View>
    </View>
  );
};
