interface ObjectCondition {
  [key: string]: string;
}

export const imageSourceRetriever = (socialMedia: string) => {
  const images: ObjectCondition = {
    amazon: require('assets/amazon.png'),
    discord: require('assets/discord.png'),
    google: require('assets/google.png'),
    slack: require('assets/slack.png'),
    okta: require('assets/okta.png'),
    outlook: require('assets/outlook.png'),
    twitch: require('assets/twitch.png'),
    '': '',
  };

  return images[socialMedia];
};

export const socialMediaIcon = (socialMedia: string) => {
  const images: ObjectCondition = {
    facebook: 'facebook-square',
    linkedin: 'linkedin-square',
    other: 'questioncircle',
    '': socialMedia,
  };

  return images[socialMedia] || socialMedia;
};

export const socialMediaIconColor = (socialMedia: string) => {
  const colors: ObjectCondition = {
    facebook: '#4267B2',
    twitter: '#00ACEE',
    linkedin: '#2867B2',
  };

  return colors[socialMedia] || 'black';
};
