import React from 'react';
import { Platform, StyleSheet, View } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { LoadingIndicator } from 'components/LoadingIndicator/loadingIndicator';
import OnBoarding from 'screens/OnBoarding/onBoardingContainer';
import Login from 'screens/Login/loginContainer';
import SignUp from 'screens/SignUp/signupContainer';
import PasswordGenerator from 'screens/PasswordGenerator/passwordGeneratorContainer';
import { Drawer } from 'screens/Drawer/drawer';
import CustomStatusbar from 'components/CustomStatusbar/customStatusbar';
import { createNativeStackNavigator } from '@react-navigation/native-stack';

const Stack = createNativeStackNavigator();

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});

interface RootProps {
  loading: boolean;
  onBoardingViewed: boolean;
}

const Root = ({ loading, onBoardingViewed }: RootProps) => {
  if (onBoardingViewed === null) {
    return null;
  }

  return (
    <View style={[styles.container]}>
      <CustomStatusbar />
      <NavigationContainer>
        <Stack.Navigator>
          {loading && (
            <Stack.Screen
              name='Loading'
              component={LoadingIndicator}
              options={{ headerShown: false }}
            />
          )}
          {!onBoardingViewed && Platform.OS !== 'web' && (
            <Stack.Screen
              name='OnBoarding'
              component={OnBoarding}
              options={{ headerShown: false }}
            />
          )}
          <Stack.Screen name='Login' component={Login} options={{ headerShown: false }} />
          <Stack.Screen name='SignUp' component={SignUp} options={{ headerShown: false }} />
          <Stack.Screen
            name='PasswordGenerator'
            component={PasswordGenerator}
            options={{
              headerStyle: {
                backgroundColor: '#3091e0',
              },
              headerTintColor: '#FFF',
            }}
          />
          <Stack.Screen name='Tutorial' component={OnBoarding} options={{ headerShown: false }} />
          <Stack.Screen
            name='Drawer'
            component={Drawer}
            options={{
              headerShown: false,
            }}
          />
        </Stack.Navigator>
      </NavigationContainer>
    </View>
  );
};

export default Root;
