import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { passwordsCollection } from 'services/firebase';
import { getPasswordsFromFirebase, selectPasswords } from 'reduxStore/slices/passwordSlice';
import useSocialMediaPicked from 'hooks/useSocialMediaFromFirebase';

const usePasswordsFromFirebase = () => {
  const dispatch = useDispatch();
  useSocialMediaPicked();
  const passwords = useSelector(selectPasswords);

  useEffect(() => {
    const unsubscribePasswordsCollection = passwordsCollection.onSnapshot(() => {
      dispatch(getPasswordsFromFirebase());
    });

    // remove those next once the component got unmounted
    return () => {
      unsubscribePasswordsCollection();
    };
  }, []);

  return passwords;
};

export default usePasswordsFromFirebase;
