import React, { Dispatch, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Text, TextInput, View } from 'react-native';
import { Picker } from '@react-native-picker/picker';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { Entypo } from '@expo/vector-icons';
import { I18n } from 'i18n-js';

import {
  selectIsCreateMode,
  selectIsEditMode,
  selectPassword,
  selectPasswordIdPicked,
} from 'reduxStore/slices/passwordSlice';
import { selectSocialMediaList, setSocialMediaPicked } from 'reduxStore/slices/socialMediaSlice';

import { LoadingIndicator } from 'components/LoadingIndicator/loadingIndicator';
import { cardView, shadow } from 'screens/PasswordGenerator/styles';
import usePasswordDescription from 'hooks/usePasswordDescription';
import useSocialMediaPicked from 'hooks/useSocialMediaPicked';
import { appColors } from 'utils/constants';
import handleCreateEditPassword from 'utils/handleCreateEditPassword';
import { AnyAction } from '@reduxjs/toolkit';
import { createEditPassword } from './styles';

export const CreateEditPasswordConfigurator = ({
  navigation,
  translations,
  modalTranslations,
}: Props) => {
  const [description, setDescription] = useState<string>('');
  const dispatch: Dispatch<AnyAction> = useDispatch();
  const passwordDescriptionMaxLen = 60;

  const socialMediaPicked: string = useSocialMediaPicked();
  const passwordDescriptionPicked: string = usePasswordDescription();

  const socialMediaList = useSelector(selectSocialMediaList);
  const isCreateMode: boolean = useSelector(selectIsCreateMode);
  const isEditMode: boolean = useSelector(selectIsEditMode);
  const passwordIdPicked: string = useSelector(selectPasswordIdPicked);
  const password: string = useSelector(selectPassword);

  const createEditButtonLabel = translations.t(isCreateMode ? 'createButton' : 'editButton');

  const handleOnPress = () => {
    handleCreateEditPassword({
      dispatch,
      navigation,
      data: {
        isEditMode,
        isCreateMode,
        password,
        passwordIdPicked,
        description,
        socialMedia: socialMediaPicked,
      },
      modalTitle: modalTranslations.t('editModalTitle'),
      modalMessage: modalTranslations.t('editModalMessage'),
    });
  };

  if (socialMediaList.length < 1) return <LoadingIndicator />;

  return (
    <View style={[cardView.container, shadow.container, createEditPassword.container]}>
      <View style={createEditPassword.userInputContainer}>
        <Text style={createEditPassword.textLabel}>{translations.t('socialMedia')}</Text>
        <Picker
          // ref={pickerRef}
          style={createEditPassword.dropdown}
          selectedValue={socialMediaPicked}
          onValueChange={(itemValue) => {
            dispatch(setSocialMediaPicked({ socialMediaPicked: itemValue }));
          }}
        >
          {socialMediaList.map((item: { id: string; name: string }) => {
            const label = item.name.charAt(0).toUpperCase() + item.name.slice(1);
            return <Picker.Item key={item.id} label={label} value={item.name} />;
          })}
        </Picker>
      </View>

      <View style={createEditPassword.userInputContainer}>
        <Text style={createEditPassword.textLabel}>{translations.t('description')}:</Text>
        <TextInput
          style={createEditPassword.textInput}
          placeholder={translations.t('descriptionPlaceholder')}
          maxLength={passwordDescriptionMaxLen}
          value={description || passwordDescriptionPicked}
          onChangeText={(text: string) => setDescription(text)}
        />
        <Text style={createEditPassword.descriptionLengthIndicator}>
          {description ? description.length : passwordDescriptionPicked.length}/
          {passwordDescriptionMaxLen}
        </Text>
      </View>

      <Entypo.Button
        style={createEditPassword.button}
        name={isCreateMode ? 'save' : 'edit'}
        size={24}
        color={appColors.textTint}
        backgroundColor={appColors.primary}
        onPress={handleOnPress}
      >
        {createEditButtonLabel}
      </Entypo.Button>
    </View>
  );
};

interface Props {
  navigation: DrawerNavigationProp<any> | NativeStackNavigationProp<any>;
  translations: I18n;
  modalTranslations: I18n;
}
