import React from 'react';
import { View, TouchableOpacity } from 'react-native';
import Svg, { G, Circle } from 'react-native-svg';
import { AntDesign } from '@expo/vector-icons';
import { appColors } from 'utils/constants';
import { styles } from './styles';

export const NextButton = ({
  size,
  strokeWidth,
  center,
  radius,
  circumference,
  progressRef,
  scrollTo,
}: Props) => (
  <View style={styles.container}>
    <Svg width={size} height={size}>
      {/* @ts-ignore */}
      <G rotation='-90' origin={center}>
        <Circle
          stroke={appColors.maximumTintColorSlider}
          cx={center}
          cy={center}
          r={radius}
          strokeWidth={strokeWidth}
        />
        <Circle
          ref={progressRef}
          stroke={appColors.primaryDark}
          cx={center}
          cy={center}
          r={radius}
          strokeWidth={strokeWidth}
          strokeDasharray={circumference}
          // strokeDashoffset={circumference - (circumference * 100) / 100}
        />
      </G>
    </Svg>
    <TouchableOpacity style={styles.button} activeOpacity={0.6} onPress={scrollTo}>
      <AntDesign name='arrowright' size={20} color='#FFF' />
    </TouchableOpacity>
  </View>
);

interface Props {
  size: number;
  strokeWidth: number;
  center: number;
  radius: number;
  circumference: number;
  progressRef: React.RefObject<View>;
  scrollTo: () => void;
}
