import React, { useCallback } from 'react';
import { Platform, TouchableOpacity } from 'react-native';
import { Feather } from '@expo/vector-icons';

import { popoverMessage } from 'utils/constants';
import { showInfoMessage } from 'utils/infoMessages';
import { useDispatch } from 'react-redux';
import { setSnackbarMessage, setSnackbarVisible } from 'reduxStore/slices/uiElementsSlice';

export const CustomPopover = ({ touchableRef }: Props) => {
  const dispatch = useDispatch();

  const onPress = useCallback(() => {
    if (Platform.OS === 'android') {
      showInfoMessage(popoverMessage);
    } else {
      dispatch(setSnackbarMessage({ snackbarMessage: popoverMessage }));
      dispatch(setSnackbarVisible({ snackbarVisible: true }));
    }
  }, []);

  return (
    <TouchableOpacity ref={touchableRef} onPress={onPress}>
      <Feather name='info' size={20} color='black' />
    </TouchableOpacity>
  );
};

interface Props {
  touchableRef: any;
}
