export type SVGProps = Readonly<{
  width?: number;
  height?: number;
  fillOpacity?: number;
  variant?: SVGVariant;
}>;

export type SVGVariant = Readonly<{
  fillColor: string;
  bgFillColor?: string;
}>;

const byDefault: SVGVariant = {
  fillColor: '#000',
  bgFillColor: '#FFF',
};

export const Variant = {
  byDefault,
};
