import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  getSocialMediaListFromFirebase,
  selectSocialMediaList,
} from 'reduxStore/slices/socialMediaSlice';
import { socialMediaCollection } from 'services/firebase';

const useSocialMediaFromFirebase = () => {
  const dispatch = useDispatch();
  const socialMediaList = useSelector(selectSocialMediaList);

  useEffect(() => {
    const unsubscribeSocialMediaCollection = socialMediaCollection.onSnapshot(() => {
      dispatch(getSocialMediaListFromFirebase());
    });

    // remove those next once the component got unmounted
    return () => {
      unsubscribeSocialMediaCollection();
    };
  }, []);

  return socialMediaList;
};

export default useSocialMediaFromFirebase;
