import React from 'react';
import { View, Text, TextInput, TouchableOpacity, Platform } from 'react-native';
import { NavigationProp } from '@react-navigation/native';
import { shadow } from 'screens/PasswordGenerator/styles';
import Icon from 'components/SVGs/Icon';
import styles from 'screens/Login/styles';
import { appColors } from 'utils/constants';
import { I18n } from 'i18n-js';

const webStyles: string =
  Platform.OS === 'web'
    ? `
    self-center
    w-full
    sm:w-11/12
    md:w-1/2
    lg:w-5/12
    xl:w-[28%]`
    : '';

export const SignUp = ({
  navigation,
  translations,
  username,
  handleSignUp,
  handleOnChangeText,
  handleOnKeyPress,
}: Props) => (
  <View className={webStyles} style={styles.container}>
    <Icon
      name='PassvaultLogoIcon'
      width={130}
      height={130}
      variant={{ fillColor: appColors.primary }}
    />
    <View style={styles.inputContainer}>
      <TextInput
        placeholder={translations.t('usernamePlaceholder')}
        value={username}
        style={[styles.input, shadow.container]}
        onChangeText={handleOnChangeText}
        onKeyPress={handleOnKeyPress}
      />
    </View>

    <View style={styles.buttonContainer}>
      <TouchableOpacity onPress={handleSignUp} style={styles.button}>
        <Text style={styles.buttonText}>{translations.t('signupButton')}</Text>
      </TouchableOpacity>
    </View>

    <View style={styles.linkContainer}>
      <TouchableOpacity onPress={() => navigation.navigate('Login')} style={styles.link}>
        <Text style={styles.linkText}>{translations.t('goBack2Login')}</Text>
      </TouchableOpacity>
    </View>
  </View>
);

interface Props {
  navigation: NavigationProp<any>;
  translations: I18n;
  username: string;
  handleSignUp: () => void;
  handleOnChangeText: () => void;
  handleOnKeyPress: () => void;
}
