import {
  savePassword2Firebase,
  setPasswordDescriptionPicked,
  setPasswordIdPicked,
} from 'reduxStore/slices/passwordSlice';
import { setModalMessage, setModalTitle, setModalVisible } from 'reduxStore/slices/uiElementsSlice';

const handleCreateEditPassword = ({
  dispatch,
  navigation,
  data,
  modalTitle,
  modalMessage,
}: Params) => {
  const { isEditMode, isCreateMode, password, passwordIdPicked, description, socialMedia } = data;

  const payload = {
    password,
    description,
    socialMedia,
  };

  if (isEditMode) {
    dispatch(setPasswordDescriptionPicked({ passwordDescriptionPicked: description }));
    dispatch(setPasswordIdPicked({ passwordIdPicked }));
    dispatch(setModalTitle({ modalTitle }));
    dispatch(setModalMessage({ modalMessage }));
    dispatch(setModalVisible({ modalVisible: true }));
  } else if (isCreateMode) {
    dispatch(savePassword2Firebase(payload));
    navigation.navigate('PasswordList');
  }

  return true;
};

interface Params {
  // eslint-disable-next-line no-unused-vars
  dispatch: (arg0: { payload: any; type: string }) => void;
  // eslint-disable-next-line no-unused-vars
  navigation: { navigate: (arg0: string) => void };
  data: {
    isEditMode: boolean;
    isCreateMode: boolean;
    password: string;
    passwordIdPicked: any;
    description: string;
    socialMedia: string;
  };
  modalTitle: string;
  modalMessage: string;
}

export default handleCreateEditPassword;
