import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { selectIsEditMode, selectPasswordDescriptionPicked } from 'reduxStore/slices/passwordSlice';
import { defaultEmptyPasswordDescription } from 'utils/constants';

const usePasswordDescription = () => {
  const [description, setDescription] = useState<string>('');
  const isEditMode = useSelector(selectIsEditMode);
  const passwordDescriptionPicked = useSelector(selectPasswordDescriptionPicked);

  useEffect(() => {
    if (isEditMode) {
      setDescription(
        passwordDescriptionPicked !== defaultEmptyPasswordDescription
          ? passwordDescriptionPicked
          : ''
      );
    }
  }, []);

  return description;
};

export default usePasswordDescription;
